    export default {
        data() {
            return {
                mobile: '',
                code: '',
                sending: false,
                sendTime: '获取验证码',
                count: 60,
            }
        },
        methods: {
            forget() {
                uni.navigateTo({
                    url: '/pages/public/pwd'
                });
            },
            register() {
                uni.navigateTo({
                    url: '/pages/public/register'
                });
            },
            inputChange(e) {
                const key = e.currentTarget.dataset.key;
                this[key] = e.detail.value;
            },
            navBack() {
                uni.navigateBack();
            },
            toLogin() {
                this.$queue.loginClear();
                let openid = this.$queue.getData("openid");
                const {mobile, code} = this;
                if (!mobile && !code) {
                    return;
                }
                this.$queue.showLoading("正在登录中...");
				var deviceInfo = this.$appH5.getDeviceInfo();
				console.log("-------设备----begin--------");
				console.log(deviceInfo.id);
				console.log(deviceInfo.name);
				console.log("-------设备----end--------");
                this.$Request.post(this.$Api.member.UserLogin, {
                    UserName: mobile,
					Password: code,
                    DeviceNo:deviceInfo.id,
					DeviceName:deviceInfo.name,
                    openid: openid
                }).then(res => {
					console.log(res);
                    if (res.Code === 1) {
                        this.$queue.setData("EndpointAccessToken", res.Message.EndpointAccessToken);
						this.$queue.setData("EndpointRefreshToken", res.Message.EndpointRefreshToken);
						this.$queue.setData("DeviceNo", deviceInfo.id);
                        this.$queue.setData("userId", res.Message.UserId);
                        this.$queue.setData("mobile", mobile);
                        this.getUserInfo(res.Message.UserId, res.Message.EndpointAccessToken);
                    } else {
                        uni.hideLoading();
                        uni.showModal({
                            showCancel: false,
                            title: '登录失败',
                            content: res.Message,
                        });
                    }
                });
            },
            getUserInfo(userId, token) {
                this.$Request.getT(this.$Api.member.GetUserInfoById).then(res => {
					console.log(res);
                    if (res.Code === 1) {
                        this.$queue.setData("image_url", res.Message.HeadImgUrl ? this.$Url + res.Message.HeadImgUrl : '/static/img/common/logo.jpg');//头像
                        this.$queue.setData("relation_id", res.Message.UserGroupId);//组别序号
                        this.$queue.setData("relation", res.Message.MUserGroup.UserGroupName); //组别名称
                        this.$queue.setData("grade", res.Message.MUserGroup.RankId); //等级值
                        this.$queue.setData("isInvitation", "1");//是否邀请
                        this.$queue.setData("nickName", res.Message.NickName ? res.Message.NickName : res.Message.Mobile);//昵称
                        this.$queue.setData("gender", parseInt(res.Message.Gender));//性别
                        let href = this.$queue.getData("href");
                        if (href) {
							console.log(href);
                            if (href==='/pages/member/publisher') {
                                this.$queue.remove("href");
                                uni.redirectTo({
                                    url: '/pages/member/publisher?uid=' + userId + '&token=' + token
                                });
                            } else if (href==='/pages/member/user') {
                                this.$queue.remove("href");
                                uni.switchTab({
                                    url: '/pages/member/user'
                                });
                            } else {
                                this.$queue.remove("href");
                                uni.redirectTo({
                                    url: href
                                });
                            }
                        } else {
                            uni.switchTab({url: '/pages/index/index'});
                        }
                    } else {
                        uni.showModal({
                            showCancel: false,
                            title: '登录失败',
                            content: res.msg,
                        });
                        this.$queue.logout();
                    }
                    uni.hideLoading();
                });
            }
        },

    }
